.addProduct {
    background-color: #4CAF50;
    /* Mu nn */
    border: none;
    /* Khng c ng vin */
    color: white;
    /* Mu ch */
    padding: 10px 20px;
    /* Khong cch m */
    text-align: center;
    /* Canh gia ni dung */
    text-decoration: none;
    /* Khng c gch chn */
    display: inline-block;
    /* Hin th dng khi */
    font-size: 16px;
    /* C ch */
    margin: 4px 2px;
    /* Khong cch gia cc phn t */
    cursor: pointer;
    /* Hin th kiu con tr */
    position: relative;
    right: 0;
}